@desktop-width: 1366px;
@big-desktop-width: 1920px;


.page {
  height: 100%;
  font-size: 20px;
  // background-color: bisque;
  box-sizing: border-box;
}


.page-body {
  // background-color: aquamarine;
  width: 375px;
  margin: 0 auto;

  @media (min-width: @desktop-width) {
    width: @desktop-width;
  }
}


a,
h1,
h2,
h3,
ul,
li,
h1,
h2,
h3,
p {
  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
}


.button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-radius: 66px;
  font-family: "Golos Text";

}

.padding-box {
  padding: 0 20px;

  @media (min-width: @desktop-width) {
    padding: 0 76px;
  }
}

svg circle:hover {
  fill: #FBCE51;
}